@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
*,*::before,*::after{margin:0;box-sizing:border-box}body{font-family:"Roboto",sans-serif}
.app{text-align:center}
.currenttime-container{display:flex;flex-direction:column;justify-content:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;align-items:flex-start;font-size:1.5em}.currenttime-day-hours{display:flex}.currenttime-day-hours div:nth-child(1){padding-right:.2em}.currenttime-hours{width:-webkit-max-content;width:-moz-max-content;width:max-content}@media screen and (min-width: 800px){.currenttime-container{flex-direction:row;align-items:center;justify-content:space-around;width:60%}}
.humidity-container{display:flex;justify-content:center;width:100%;align-items:center;padding:0 .2em}.humidity-container img{margin-right:.3em;height:32px;width:32px}
.pressure-container{display:flex;justify-content:center;width:100%;align-items:center;padding:0 .2em}.pressure-container img{margin-right:.3em;height:32px;width:32px}
.temperature-container{display:flex;justify-content:center;width:100%;align-items:center;padding:0 .2em}.temperature-container img{margin-right:.3em;height:32px;width:32px}
.weather-description-container{display:flex;padding:0 .2em;align-items:center;justify-content:center;width:50%}.weather-description-container img{border-radius:50%;background-color:#7692e6;margin-left:.5em;height:64px;width:auto}.desc{padding-left:.6em}
.wind-container{display:flex;justify-content:center;width:100%;align-items:center;padding:0 .2em}.wind-container img{height:32px;width:32px;margin-right:.3em}
.current-weather-container{padding:.2em;width:100%;text-shadow:1px 1px 1px #000}.current-weather-icon-container{display:flex;flex-direction:column;align-items:center;background-color:#e8eff3}.current-weather-time-desc{display:flex;align-self:start;width:100%;background-color:#555;color:#eee;padding:1em 0;border:2px solid #f0f8ff;border-radius:5px}.current-weather-details{width:100%;display:grid;grid-gap:2em;gap:2em;justify-content:space-around;grid-template-columns:repeat(2, 1fr);border:2px solid #f0f8ff;border-radius:5px;background-color:#555;color:#eee;padding:1em 0}.current-weather-details>div{font-size:1.5em;justify-content:start;padding-left:1em}@media screen and (min-width: 800px){.current-weather-container{padding:.2em;width:70%;margin-left:15%}.current-weather-details{margin-top:0;padding:2em .2em}.current-weather-details>div{font-size:1.5em;justify-content:start;padding-left:3em}.current-weather-time-desc{justify-content:space-around;align-items:center;align-self:center}.current-weather-icon-container{padding:2em 1em;align-items:center;justify-content:center}}@media screen and (min-width: 1200px){.current-weather-container{padding:.2em;width:50%;margin-left:25%}}
.line-chart-container{display:flex;height:250px;width:100%;justify-self:center;align-self:center;background-color:#e8eff3;border:2px solid #f0f8ff;border-radius:5px}@media screen and (min-width: 400px){.line-chart-container{height:400px}}@media screen and (min-width: 950px){.line-chart-container{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:70%;justify-content:center;align-items:center;margin:0 15%;padding-bottom:1em}}
